import Vue from 'vue'
import Vuex from 'vuex'
// 引入axios
import Axios from 'axios'
Axios.defaults.baseURL = 'http://101.37.33.51:8888'
// 请求拦截器
Axios.interceptors.request.use(function (config) {
  // code   (config.headers  ---请求头对象)
  let token = window.sessionStorage.getItem('token')
  if (!token) return config
  config.headers.authorization = token
  return config
})
Vue.prototype.$http = Axios
Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    // 图片查看器的URL和开关
    appbigUrl: '',
    appimgflage:false,
    // 页面加载开关
    loadingflage:false,
  },
  mutations: {
    // 设置图片查看器的值
    setAppimgFlage(state,val){
      state.appimgflage = val[0]
      if(!val[0]) return
      state.appbigUrl = val[1]
    },
    // 设置页面加载开关
    setLoadingFlage(state,val){
      state.loadingflage = val
    }
  },
  actions: {
  },
  modules: {
  }
})
