<template>
  <div id="app" class="souye App">
    <!-- ============公共使用区域====================== -->
    <!-- 大图查看器 -->
    <el-dialog white="100%" :visible.sync="appimgflage" :before-close="appimgfalse">
      <a target="_blank" :href="appbigUrl | imgFilter" class="imgbtn" slot="title">查看原图</a>
      <img @click="setAppimgFlage([false])" id="imgBox" :src="appbigUrl | imgFilter" alt />
    </el-dialog>
    <!-- =================================== -->
    <div class="top">
      <i v-if="myHeadimg==''" class="login el-icon-user" @click="loginFlage = true"></i>
      <img
        v-if="myHeadimg != ''"
        class="login"
        :src="myHeadimg | isNull | imgFilter"
        @click="$route.path=='/usercontrol' ? $router.push('/') : $router.push('/usercontrol')"
        alt
      />
      <el-tooltip class="item" effect="dark" content="选择并输入关键字" placement="bottom">
        <i class="email">
          <el-input
            id="topsou"
            placeholder="请输入内容"
            size="mini"
            v-model="inputval"
            class="input-with-select"
            effect="dark"
          >
            <el-select slot="prepend" v-model="selectval" placeholder="请选择" style="width:85px">
              <el-option label="用户名" value="1"></el-option>
              <el-option label="手机号" value="2"></el-option>
              <el-option label="文章" value="3"></el-option>
            </el-select>
            <el-button slot="append" icon="el-icon-search" style="color:red"></el-button>
          </el-input>
        </i>
      </el-tooltip>
    </div>
    <router-view v-if="aa" style="width:100%;height:100%;overflow: auto;"></router-view>
    <div class="bottom">
      <div>首页原创：赵良翔</div>
      <div>站主：舒榆衡</div>
      <div>个人联系电话：17768938295</div>
    </div>
    <!-- 登录输入框 -->
    <el-dialog :title="logisreg?'登录':'注册'" :visible.sync="loginFlage" width="80%" id="loginDlog">
      <el-form ref="form">
        <el-form-item>
          <el-input placeholder="请输入手机号" v-model="username" prefix-icon="el-icon-user"></el-input>
        </el-form-item>
        <el-form-item>
          <el-input
            :type="pwdview ? 'text': 'password'"
            placeholder="请输入密码"
            v-model="password"
            prefix-icon="el-icon-cpu"
          >
            <i
              @click="pwdview = !pwdview"
              class="pwdview"
              slot="suffix"
              :class="pwdview ? 'el-icon-unlock': 'el-icon-lock'"
            ></i>
          </el-input>
        </el-form-item>
        <el-form-item v-if="!logisreg">
          <el-input
            style="color:red"
            v-model="InvitationCode"
            placeholder="请输入邀请注册码"
            prefix-icon="el-icon-finished"
          ></el-input>
        </el-form-item>
      </el-form>
      <i class="bottom_title">{{logisreg ? '请登录您的博客' : '没有邀请码？请联系邮箱：2285128399@qq.com'}}</i>
      <span slot="footer" class="dialog-footer">
        <i @click="logisreg = true" v-if="!logisreg" class="logtext">已经账号前往登录?</i>
        <el-button @click="falseBtn">{{logisreg ? '注 册' : '取消'}}</el-button>
        <el-button type="primary" @click="trueBtn">{{logisreg ? '登 录' : '注册'}}</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
// 引入vuex
import { mapState, mapMutations } from "vuex";
export default {
  computed: {
    ...mapState([
      "appbigUrl", //查看大图图片路径
      "appimgflage", //是否打开查看大图
      "loadingflage" //是否打开加载动画
    ])
  },
  data() {
    return {
      aa: true,
      // myHeadimg:'http://192.168.0.104:8888/upload_files/2020-2-17/hua.jpg',
      myHeadimg: "",
      // myHeadimg: null,
      // 是否显示密码
      pwdview: false,
      selectval: "",
      inputval: "",
      // 登录输入框
      loginFlage: false,
      logisreg: true,
      // 输入框对应数据
      username: "17768938296",
      password: "syh654321",
      InvitationCode: "",
      lodeingel: ""
    };
  },
  methods: {
    ...mapMutations([
      "setAppimgFlage", //修改图片查看器的开关 参数 [true/false,imgurl]
      "setLoadingFlage" //设置加载动画的开关 参数 true/false
    ]),
    appimgfalse() {
      this.setAppimgFlage([false]);
    },
    async trueBtn() {
      //判断是否是登录
      if (this.logisreg) {
        this.setLoadingFlage(true);
        let { data: res } = await this.$http.post("/users/login", {
          phone: this.username,
          password: this.password
        });
        this.setLoadingFlage(false);
        if (res.ok == 0)
          return this.$notify.error({
            title: res.error,
            customClass: "alter"
          });

        this.$notify.success({
          title: "登录成功~",
          customClass: "alter"
        });
        this.myHeadimg = res.head_src;
        sessionStorage.setItem("myHeadimg", res.head_src);
        // 保存token
        sessionStorage.setItem("token", res.token);
        // 刷新页面
        this.aa = false;
        let that = this;
        setTimeout(() => {
          that.aa = true;
        }, 0);
        // // 关闭登录框
        this.loginFlage = false
        // console.log('执行刷新')
      } else {
        this.setLoadingFlage(true);
        // 请求接口
        this.$http
          .post("/users", {
            phone: this.username,
            password: this.password,
            auth_code: this.InvitationCode
          })
          .then(data => {
            this.setLoadingFlage(false);
            // 如果报错
            if (data.data.ok == 0)
              return this.$notify.error({
                title: data.data.error,
                customClass: "alter"
              });
            // 成功提示
            this.$notify.success({
              title: data.data.msg,
              customClass: "alter"
            });
            // 如果注册成功
            this.logisreg = true;
          });
      }
    },
    falseBtn() {
      // 判断是注册还是取消
      if (this.logisreg) {
        this.logisreg = false;
      } else {
        this.loginFlage = false;
      }
    },
    // 整页加载函数
    loading() {
      if (this.loadingflage) {
        this.lodeingel = this.$loading({
          // lock: true,
          text: "Loading",
          spinner: "el-icon-loading",
          background: "rgba(0, 0, 0, 0.7)"
        });
      } else {
        this.lodeingel.close();
      }
    }
  },
  // 监听属性
  watch: {
    loadingflage: {
      handler() {
        this.loading();
      }
    }
  },
  filters: {
    isNull(val) {
      return val == null ? "/upload_files/2020-2-17/10k.jpg" : val;
    }
  },
  created() {
    let myHeadimg = sessionStorage.getItem("myHeadimg");
    if (myHeadimg) this.myHeadimg = myHeadimg;
    if (myHeadimg == "null") this.myHeadimg = null;
  }
};
</script>
  
